.nav{
    position: fixed;
    top: 0;
    width: 100%;
    padding: 20px;
    height: 30px;
    display: flex;
    z-index: 1;
    justify-content: space-between;

    transition-timing-function: ease-in ;
    transition: all 0.5s;
    
}
.nav_logo{
    position: fixed;
    left: 20;
    width: 80px;
    object-fit: contain;
}
.nav_ava{
    position: fixed;
    right: 20px;
    width: 30px;
    object-fit: contain ;
}
.nav_flat{
    background-color: #111;
}